/**
 * Sizes
 */
const sizes = {
    width: window.innerWidth,
    height: window.innerHeight
}

window.addEventListener('resize', () =>
{
    // Update sizes
    sizes.width = window.innerWidth
    sizes.height = window.innerHeight

    // Update camera
    camera.aspect = sizes.width / sizes.height
    camera.updateProjectionMatrix()

    // Update renderer
    renderer.setSize(sizes.width, sizes.height)
    renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))
})





// World
const world = new CANNON.World()
world.broadphase = new CANNON.SAPBroadphase(world)
world.allowSleep = true
world.gravity.set(0, -9.82, 0)

const defaultMaterial = new CANNON.Material('concrete')

const defaultContactMaterial = new CANNON.ContactMaterial(
    defaultMaterial,
    defaultMaterial,
    {
        friction: 0.1,
        restitution: 0.4
    }
)
world.addContactMaterial(defaultContactMaterial)
world.defaultContactMaterial = defaultContactMaterial

const floorShape = new CANNON.Plane()
const floorBody = new CANNON.Body({
    mass: 0,
    shape: floorShape
})
floorBody.material = defaultMaterial
floorBody.position.set(0, 0.05, 0)
floorBody.quaternion.setFromAxisAngle(
    new CANNON.Vec3(-1, 0, 0),
    Math.PI / 2
)
world.addBody(floorBody)



// Main
const main = {
    size: 0.2,
    speed: 4,
    mass: 1,
    position: {
        x: 0.4,
        y: 2.5,
        z: 0
    }
}
const createMain = () => {
    main.mesh = new THREE.Mesh(
        new THREE.SphereBufferGeometry(1, 64, 64),
        new THREE.MeshStandardMaterial({
            metalness: 0.1,
            roughness: 0.4,
            color: 0x564086
        })
    )
    main.mesh.scale.set(main.size, main.size, main.size)
    main.mesh.castShadow = true
    main.mesh.position.copy(main.position)
    scene.add(main.mesh)
    
    main.body = new CANNON.Body({
        mass: main.mass,
        shape: new CANNON.Sphere(main.size),
        material: defaultMaterial
    })
    main.body.position.copy(main.position)
    world.addBody(main.body)
}
