// Camera
const camera = new THREE.PerspectiveCamera(50, sizes.width / sizes.height, 0.001, 1000)
camera.position.set(prop.camera.position.x, prop.camera.position.y, prop.camera.position.z)
camera.lookAt(new THREE.Vector3(0, 0, 0))
scene.add(camera)

const camUpdate = () => {
    camera.fov = prop.camera.zoom
    camera.position.set(prop.camera.position.x, prop.camera.position.y, prop.camera.position.z)
    camera.lookAt(new THREE.Vector3(prop.camera.look.x, prop.camera.look.y, prop.camera.look.z))
    camera.updateProjectionMatrix()
}

// gui.add(prop.camera.position, 'x').min(-3).max(3).step(-0.001).name('Cam X').onChange(camUpdate)
