// Controls
const controls = new OrbitControls(camera, canvas)
controls.enableDamping = true

/**
 * Renderer
 */
const renderer = new THREE.WebGLRenderer({
    canvas: canvas
})
renderer.shadowMap.enabled = true
renderer.shadowMap.type = THREE.PCFSoftShadowMap
renderer.setSize(sizes.width, sizes.height)
renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))
renderer.setClearColor(0xfbfbfb)

/**
 * Animate
 */
const clock = new THREE.Clock()
let previousTime = 0

const tick = () =>
{
    const elapsedTime = clock.getElapsedTime()
    const deltaTime = elapsedTime - previousTime
    previousTime = elapsedTime

    world.step(1/60, deltaTime, 3)

    // main.mesh.quaternion.copy(main.body.quaternion)
    // main.mesh.position.copy(main.body.position)

    // document.getElementById('pos').innerHTML = 'x: '+Math.round(main.body.position.x * 1000) / 1000+'<br>y: '+Math.round(main.body.position.y * 1000) / 1000+'<br>z: '+Math.round(main.body.position.z * 1000) / 1000

    for (const model of objects) {
        model.mesh.quaternion.copy(model.body.quaternion)
        model.mesh.position.copy(model.body.position)
    }
    
    // Update controls
    controls.update()

    // Render
    renderer.render(scene, camera)

    // Call tick again on the next frame
    window.requestAnimationFrame(tick)
}

tick()